import React from 'react';
import CountUp from 'react-countup';
import './michalowo.css';

const Mich = () => {
    const value2 = <CountUp start={50} end={0} duration={20} separator=" " />
    return (
        <div className="mich">Zainfekowani w Michałowie: {value2}</div>
    );
}

export default Mich;
