import axios from 'axios';

const url = 'https://covid19.mathdro.id/api/countries/poland';

export const fetchData = async () => {
    let myUrl = url;


    try {
        const { data: { confirmed, recovered, deaths, lastUpdate } } = await axios.get(myUrl);

        return { confirmed, recovered, deaths, lastUpdate };
    } catch (error) {
        return error;
    }
};