import React from 'react';
import './Infected.css';
import CountUp from 'react-countup';

const Infected = (props) => {
    const { value } = props.score;
    const value2 = <CountUp start={value} end={value + 15} duration={1000} separator=" " />
    return (
        <div className="infected">
            <h1>Liczba osób zarażonych</h1>
            <p>Do dnia {props.data} : {value2} osób</p>
        </div>
    );
}

export default Infected;