import React from 'react';
import CountUp from 'react-countup';
import './Deaths.css';
const Deaths = (props) => {
    const { value } = props.score;
    const value2 = <CountUp start={value - 100} end={value} duration={10} separator=" " />
    return (

        <div className="deaths">
            <h1>Liczba zgonów</h1>
            <p>Do dnia {props.data} : <span>{value !== null ? value2 : "Ładowanie danych..."}</span> osób</p>
        </div>
    );
}

export default Deaths;